:root {
  --light-green: hsl(120, 38%, 85%);
  --light-tan: hsl(7, 31%, 95%);
  --faded-pink: hsl(326, 43%, 80%);
  --pastel-pink1: hsl(303, 58%, 74%);
  --pastel-pink2: hsl(316, 69%, 61%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--dark-bg);
  color: var(--dark-secondary-text-color);
}

h1, h2, h3 {
  margin-bottom: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 64em) {
  h1 {
    font-size: 1.75rem;
  }
}

a, a:visited, a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 64em) {
  .container {
    padding: 0;
  }
}

.header {
  margin-top: 1.625rem;
  background-color: var(--light-green);
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--dark-primary-text-color);
}